ion-content {
  --ion-background-color: var(--ion-color-dark);
}

.ion-page {
  background-color: var(--ion-color-black);
}

ion-toolbar {
  --min-height: 8vh !important;
}

ion-header {
  position: sticky;
  top: 0;
}

ion-menu > * {
  background-color: var(--ion-menu-background);
}

ion-label {
  color: white !important;
  font-size: 1.25rem !important;
}

.footer {
  position: sticky;
  bottom: 0;
  left: 0;
  width: 100%;
}

.btn-sm {
  height: 45px !important;
  width: 45px !important;
}

.btn-md {
  height: 60px !important;
  width: 60px !important;
}

.btn-lg {
  height: 65px !important;
  width: 65px !important;
}

.nav-icon-sm {
  font-size: 1.5rem;
}

.nav-icon-md {
  font-size: 2.5rem;
}

.nav-icon-lg {
  font-size: 3.5rem;
}

.toolbar-container {
  min-height: unset !important;
  height: 75px !important;
}

.page-content {
  max-width: 550px;
  width: 95%;
}

.w-100 {
  width: 100%;
}

.horizontal-start-vertical-center {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  flex-direction: row-reverse;
}

.horizontal-center {
  display: flex;
  justify-content: center;
  flex-direction: row;
}

.horizontal-end-vertical-center {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
