:root {
  --component-shadow: -1px 3px 5px 0px #00000050;
  --component-shadow-reverse: -1px -3px 5px 0px #00000050;
}

.shadow {
  box-shadow: var(--component-shadow);
  -webkit-box-shadow: var(--component-shadow);
  -moz-box-shadow: var(--component-shadow);
}

.shadow-reverse {
  box-shadow: var(--component-shadow-reverse);
  -webkit-box-shadow: var(--component-shadow-reverse);
  -moz-box-shadow: var(--component-shadow-reverse);
}
